<script>
  import {createEventDispatcher} from "svelte";

  const dispatch = createEventDispatcher();

  export let users;
  export let user;

  let selectedUser;

  function isNotTheConnectedUser(usr){
    return user !== usr
  }

  function navigateToUserProfile(user){
    dispatch(
        'selectedUser',
        user
    );
  }
</script>

